.footer {
  background-color: $color-tangaroa;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  max-width: 1366px;
  margin: 0 auto;
  padding: 40px 15px;
  gap: 30px;

  @media (min-width: $vp-768) {
    padding: 50px 45px;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 0;
  }

  @media (min-width: $vp-1024) {
    padding: 50px 105px;
  }
}

.footer__navigation {
  display: flex;
  flex-direction: column;
  gap: 30px;

  @media (min-width: $vp-768) {
    flex-direction: row;
    gap: 25px;
    align-items: center;
  }

  @media (min-width: $vp-1024) {
    gap: 40px;
  }
}

.footer__logo {
  cursor: pointer;
  width: 42px;
  height: 22px;

  @media (min-width: $vp-768) {
    width: 38px;
    height: 20px;
  }

  @media (min-width: $vp-1024) {
    width: 70px;
    height: 38px;
  }
}

.footer__links-list {
  display: flex;
  flex-direction: column;
  gap: 23px;

  @media (min-width: $vp-768) {
    flex-direction: row;
  }

  @media (min-width: $vp-1024) {
    gap: 33px;
  }
}

.footer__link {
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  color: $color-alice-blue;

  @media (min-width: $vp-1024) {
    font-size: 14px;
    line-height: 17px;
  }

  @media (min-width: $vp-1280) {
    font-size: 16px;
  }
}

.footer__social-links-list {
  display: flex;
  align-items: center;
  gap: 22px;

  @media (min-width: $vp-1024) {
    gap: 27px;
  }
}

.footer__social-link {
  display: block;
  line-height: 0;
}

.footer__social-link-illustration {
  width: 16px;
  height: 16px;

  @media (min-width: $vp-1024) {
    width: 20px;
    height: 20px;
  }
}
